@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,300;1,400&display=swap);
*{margin:0;padding:0}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:linear-gradient(145deg, #03134e 20%, #001e88 80%);background-attachment:fixed;color:#ddecff;font-size:18px;font-family:"Source Sans Pro", sans-serif;font-family:"Fira Code", code;scroll-behavior:smooth}body a:visited,body a{color:#ddecff;text-decoration:none}#root{min-height:100vh;position:relative}.sections{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;overflow:scroll}section{width:80%;min-height:calc(100vh - 16rem);padding:6rem 0rem}section h1,section h2{font-family:"Source Sans Pro", sans-serif}section h1{font-size:4rem;color:#3cffb9;margin:1rem 0}section h2{color:#3cffb9;font-size:2rem;font-weight:600;margin-bottom:2rem}

#Landing{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;position:relative}#Landing h1,#Landing h2{font-family:"Source Sans Pro", sans-serif}#Landing h1{margin:1rem 0}#Landing h1 span{color:#d6deff}#Landing h2{color:#ddecff}#Landing #cta{justify-self:flex-end}#Landing .link-button{color:#3cffb9;display:inline-block;padding:0.6rem 1.4rem;margin-right:1rem;border:2px #3cffb9 solid;border-radius:0.3rem;transition:all 0.2s ease-in-out}#Landing .link-button:hover{color:#00ffc2;background-color:rgba(0,255,162,0.233)}#Landing #chevron{position:absolute;bottom:2rem;right:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;font-size:2.5rem;color:#3cffb9}#Landing #chevron svg{-webkit-animation:chevron 3s infinite ease-in alternate;animation:chevron 3s infinite ease-in alternate;margin-top:0;border-radius:1rem}#Landing #chevron svg:last-child{font-size:3rem;-webkit-animation-delay:-0.3s;animation-delay:-0.3s;margin-top:-1.8rem}@-webkit-keyframes chevron{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}50%{-webkit-transform:translate3d(0, 0.6rem, 0);transform:translate3d(0, 0.6rem, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@keyframes chevron{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}50%{-webkit-transform:translate3d(0, 0.6rem, 0);transform:translate3d(0, 0.6rem, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}css-doodle{--rule: (
		: doodle {
			@grid: 5; 
			grid-gap: 5px; 
			width: 10em; 
			height: 10em;
		} 
			background: @pick(#21e0b4af, #00ffa3, #28e6b9, #26dddd) ;
			border-radius: @repeat(4, @rand(40%, 60%));
		:after {
			color: black;
			content: \@hex(@rand(9632, 9687));
		}
	)}

nav{position:sticky;position:-webkit-sticky;padding:0.75rem 2rem;top:0;font-size:1.2rem;color:#3cffb9;z-index:99;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;background:rgba(255,255,255,0.2);backdrop-filter:blur(12px);-webkit-backdrop-filter:blur(15px);border:1px solid rgba(255,255,255,0.18)}nav span,nav a:visited,nav a{padding:0.3rem 0.6rem;color:#ddecff}nav a:visited,nav a{text-decoration:none;transition:all 0.2s ease-in-out}nav a:visited:hover,nav a:hover{color:#3cffb9}nav a:first-child{padding:0.4rem 1.2rem;border:2px #3cffb9 solid;margin-right:1rem;border-radius:0.3rem}nav a:first-child:hover{color:#00ffc2;background-color:rgba(0,255,162,0.233)}


.Project{width:80%;margin:auto;display:grid;grid-template-columns:5fr 6fr;margin-bottom:5rem}.Project .project-preview{border-radius:1rem}.Project .project-preview .image{position:relative;display:-webkit-inline-flex;display:inline-flex;max-width:100%;max-height:100%;border-radius:1rem;box-shadow:0.2rem 0.2rem 0 1px #00ffc2;transition:all 0.3s ease-in-out}.Project .project-preview .image::after{-webkit-transform:translate(-0.5rem, -0.5rem);transform:translate(-0.5rem, -0.5rem);border-radius:1rem;content:"";height:100%;width:100%;background:black;opacity:0.3;transition:all 0.3s ease-in-out;position:absolute;top:0;left:0}.Project .project-preview .image img{-webkit-transform:translate(-0.5rem, -0.5rem);transform:translate(-0.5rem, -0.5rem);transition:all 0.3s ease-in-out;border-radius:1rem;max-width:100%}.Project:hover .project-preview .image{box-shadow:0.1rem 0.1rem 0 1px #3cffb9}.Project:hover .project-preview .image img{-webkit-transform:translate(0, 0);transform:translate(0, 0)}.Project:hover .project-preview .image::after{opacity:0;-webkit-transform:translate(0, 0);transform:translate(0, 0)}.Project .project-info{margin:auto;height:10rem;border-radius:0.75rem;border-top-left-radius:0;border-bottom-left-radius:0;padding:0.75rem 1rem;padding-left:2rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background:#223886;-webkit-justify-content:space-evenly;justify-content:space-evenly}.Project .project-info .title{text-align:center;font-size:1.3rem;margin-bottom:1.25rem}.Project .project-info .title span{color:#3cffb9}.Project .project-info .description{position:relative;padding-right:0.5rem;font-size:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between}.Project .project-info .description .project-links{font-size:0.9rem;margin-top:1rem;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.Project .project-info .description .project-links span{color:#3cffb9;margin-right:1rem}.Project:nth-child(odd){grid-template-columns:6fr 5fr}.Project:nth-child(odd) .project-preview{-webkit-order:2;order:2}.Project:nth-child(odd) .project-info{-webkit-order:1;order:1;padding:0.75rem 1rem;padding-left:2rem;border-radius:0.75rem;border-top-right-radius:0;border-bottom-right-radius:0}

#sidelinks{position:fixed;right:0;top:calc(50vh + 2rem);margin-right:1.5rem;-webkit-transform:translateY(-50%);transform:translateY(-50%)}#sidelinks .container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}#sidelinks .container a:visited,#sidelinks .container a{transition:all 0.2s ease-in-out;color:#ddecff;text-decoration:none;padding:0.5rem 0;font-size:1.5rem;position:relative}#sidelinks .container a:visited span,#sidelinks .container a span{transition:all 0.2s ease-in-out;width:8ch;text-align:center;border-radius:0.25rem;color:#3cffb9;font-size:1rem;padding:0.5rem;display:none;position:absolute;right:3rem;top:50%;-webkit-transform:translateY(-55%);transform:translateY(-55%);background:#030e38;box-shadow:1px 1px 2px #3cffb9}#sidelinks .container a:visited:hover,#sidelinks .container a:hover{color:#3cffb9}#sidelinks .container a:visited:hover span,#sidelinks .container a:hover span{opacity:1;display:inline}#sidelinks .container::before,#sidelinks .container::after{content:"";width:0;border:solid #ddecff 1.5px;background-color:#ddecff;position:fixed;height:calc((80vh / 2) - 4rem);border-radius:2rem;right:50%}#sidelinks .container::before{top:0;-webkit-transform:translate(50%, -105%);transform:translate(50%, -105%)}#sidelinks .container::after{-webkit-transform:translate(50%, 105%);transform:translate(50%, 105%);bottom:0}

